<template>
  <v-app :dark="dark">
    <router-view/>
    <div>
      <v-btn outline large class="elevation-5" :class="textClass" @click="toggleTheme()" :color="textColor">
        {{colorText}}
      </v-btn>
      <v-alert
        :value="alert"
        type="success"
        transition="scale-transition"
        v-on:toggle-alert="setAlert"
      >
        <code class="black display-1 grey--text text--lighten-3 elevation-5">http://idea.lestat.me</code>
      </v-alert>
    </div>
  </v-app>
</template>

<script>
  import HelloWorld from '@/components/HelloWorld'
  export default {
    name: 'App',
    components: {
      HelloWorld
    },
    data() {
      return {
        dark: true,
        colorText: '白天',
        textColor: 'white',
        textClass: 'white--text',
        mainStyle: 'dark',
        alert: false
      }
    },
    methods: {
      toggleTheme() {
        this.dark = !this.dark
        this.colorText = this.colorText === '白天' ? '夜晚' : '白天';
        this.textColor = this.textColor === 'white' ? 'black' : 'white';
        this.textClass = this.textClass === 'white--text' ? 'black--text' : 'white--text';
      },
      setAlert(value) {
        console.log(value)
        this.alert = !this.alert
      }
    }
  }
</script>

<style>

</style>



