<template>
  <v-layout justify-center column>
    <div class="xs-top">

    </div>
    <div class="text-xs-center">

      <v-avatar
        :tile="tile"
        :size="avatarSize"
        color="grey lighten-4"
        class="elevation-5"
      >
        <img src="https://lestat.b0.upaiyun.com/blog/avatar.jpg">
      </v-avatar>
      <h1 class="text-xs-top">
        <code id="h1" class="black h1 grey--text text--lighten-3 elevation-5"></code>
      </h1>
      <v-btn class="elevation-5" :key="i" v-for="(item,i) in links" flat @click="jumpTo(item)">
        <v-icon>{{item[2]}}</v-icon>
        {{item[0]}}
      </v-btn>

      <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-btn class="elevation-5" slot="activator" flat>留言板
          <v-icon>dashboard</v-icon>
        </v-btn>

        <v-card>
          <v-toolbar dark color="secondary">
            <v-btn class="elevation-5" icon @click.native="dialog = false">
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>留言板</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark flat @click.native="dialog = false">关闭</v-btn>
            </v-toolbar-items>
          </v-toolbar>

          <v-divider></v-divider>
          <v-list three-line subheader class="ma-5">
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-text-field
                v-model="email"
                :rules="emailRules"
                label="邮箱地址"
                required
              ></v-text-field>
              <v-text-field
                v-model="content"
                :rules="contentRules"
                :counter="100"
                label="留言内容"
                required
                textarea
              ></v-text-field>

              <v-checkbox
                v-model="allowReply"
                label="是否接收邮件回复?"
                class="mt-3"
              ></v-checkbox>

              <v-btn
                :disabled="!valid"
                @click="submit"
                :loading="submitting"
              >
                提交
              </v-btn>
              <v-btn @click="clear">重置</v-btn>
            </v-form>
            <v-alert
              class="ma-3"
              :value="alert"
              type="warning"
              transition="scale-transition"
            >
              {{message}}
            </v-alert>
          </v-list>
        </v-card>
      </v-dialog>
      <div class="text-xs-center mt-3">
        <v-chip class="elevation-5" color="primary" text-color="white">php</v-chip>

        <v-chip class="elevation-5" color="secondary" text-color="white">mysql</v-chip>

        <v-chip class="elevation-5" color="orange" text-color="white">linux</v-chip>

        <v-chip class="elevation-5" color="warning" text-color="white">ECMAScript6</v-chip>

        <v-chip class="elevation-5" color="green" text-color="white">Scrapy</v-chip>

        <v-chip class="elevation-5" color="blue" text-color="white">Docker</v-chip>
      </div>
    </div>
  </v-layout>

</template>

<style>
  @keyframes blink {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  #h1::after {
    content: '|';
    animation: blink 500ms infinite;
  }

  #h1 {
    flex-grow: 1;
  }

  h1 {
    margin: 30px;
  }

</style>


<script>
  import theaterJS from 'theaterJS'
  import axios from 'axios'

  export default {
    name: 'Helloworld',
    data: () => ({
      alert: false,
      message: '',
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      slider: 168,
      tile: false,
      links: [['博客', 'http://lestatmiao.github.io/', 'home'], ['关于', 'https://lestatmiao.github.io/about/', 'face'], ['维基中文(PC)', 'https://wiki.lestat.me/', 'pages'], ['归档', 'https://lestatmiao.github.io/archives/', 'archive']],
      lorem: 'Lorem ipsum dolor sit amet, at aliquam vivendum vel, everti delicatissimi cu eos. Dico iuvaret debitis mel an, et cum zril menandri. Eum in consul legimus accusam. Ea dico abhorreant duo, quo illum minimum incorrupte no, nostro voluptaria sea eu. Suas eligendi ius at, at nemore equidem est. Sed in error hendrerit, in consul constituam cum.',
      valid: true,
      content: '',
      contentRules: [
        v => !!v || '留言内容不能为空',
        v => (v && v.length <= 10) || '留言内容不能大于100个字'
      ],
      email: '',
      emailRules: [
        v => !!v || '邮箱地址不能为空',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || '请输入正确的邮箱地址'
      ],
      allowReply: true,
      submitting: false
    }),
    computed: {
      avatarSize() {
        return `${this.slider}px`
      }
    },
    mounted() {
      const theater = theaterJS()
      theater.addActor('h1', {speed: 0.8, accuracy: 0.6})
      theater.addScene(1800)
      theater.addScene("h1:什么都略懂一点", 800)
      theater.addScene(800)
      theater.addScene("h1:生活更多彩一些", 800)
      theater.addScene(800)
      theater.addScene(theater.replay.bind(theater))
    },
    methods: {
      jumpTo(item) {
        if (item[3] === 'noJump') {
          this.$emit('toggle-alert')
        } else {
          window.open(item[1])
        }
      },
      submit() {
        if (this.$refs.form.validate()) {
          this.submitting = true
          const that = this
          // Native form submission is not yet supported
          axios.post('http://api.lestat.me/words', {
            content: this.content,
            email: this.email,
            allowReply: this.allowReply
          }).then(function (response) {
            that.submitting = false
            that.message = response.data.message
            that.alert = true
            console.log(response);
          }).catch(function (error) {
            that.submitting = false
            console.log(error);
          })
        }
      },
      clear() {
        this.$refs.form.reset()
        this.submitting = false
        this.alert = false
      }
    }
  }
</script>



